<template>
  <n-breadcrumb class="breadcrumb" separator-class="el-icon-arrow-right">
    <n-breadcrumb-item
      class="breadcrumb-item no-hover"
      :class="{ last: index === nodeList.length - 1 }"
      v-for="(node, index) in nodeList"
      :key="node.elementId"
      :data-zone="`${node.elementId}_${node.elementType}`"
    >
      {{ getNodeNameById(node.elementId) }}
    </n-breadcrumb-item>
  </n-breadcrumb>
</template>

<script>
  import { getParentNodeListByElementId } from '@/utils/tree-utils'
  import { pageInject } from '@/renderer/tools/pageStore'

  export default {
    name: 'Breadcrumb',
    inject: [...pageInject],
    props: {
      elementId: {
        type: String,
      },
    },
    computed: {
      nodeList() {
        const layoutData = this.getStore().layoutData
        const parentNodeList = this.elementId
          ? getParentNodeListByElementId(layoutData, this.elementId) || []
          : []
        return parentNodeList.length > 3
          ? parentNodeList.slice(-3).filter(item => !['dialog-container'].includes(item.elementType))
          : parentNodeList.filter(item => !['dialog-container'].includes(item.elementType))
      },
    },
    methods: {
      getNodeNameById(elementId) {
        return this.getElementPropsById(elementId).elementName
      },
      getElementPropsById(elementId) {
        const realId = elementId === 'app' ? this.getStore().layoutData.elementId : elementId
        return this.getStore().dynamicProps[realId] || {}
      },
    }
  }
</script>

<style lang="less" scoped>
  @import '~@/styles/index.less';
  .breadcrumb::v-deep {
    width: 100%;
    font-size: 14px;
    padding: 2px;

    .breadcrumb-item {
      &:hover {
        cursor: pointer;
        color: @drop-main-color;
      }
      & > span {
        color: #999999;
      }

      &.last {
        & > span {
          color: #333333;
        }
      }
    }
  }
</style>
